const selectAll = document.querySelector('.meal_1_select_all');
const selectAll2 = document.querySelector('.meal_2_select_all');
const selectAll3 = document.querySelector('.meal_3_select_all');
const selectAll4 = document.querySelector('.meal_4_select_all');
const selectAll5 = document.querySelector('.meal_5_select_all');
const selectAll6 = document.querySelector('.meal_6_select_all');

const checkboxes = document.querySelectorAll('.meal_1');
const checkboxes2 = document.querySelectorAll('.meal_2');
const checkboxes3 = document.querySelectorAll('.meal_3');
const checkboxes4 = document.querySelectorAll('.meal_4');
const checkboxes5 = document.querySelectorAll('.meal_5');
const checkboxes6 = document.querySelectorAll('.meal_6');

// Meal 1
selectAll.addEventListener('change', () => {
    checkboxes.forEach((checkbox) => {
        checkbox.checked = selectAll.checked;
    });
});

checkboxes.forEach((checkbox) => {
    checkbox.addEventListener('change', () => {
        if (checkbox.checked == false) {
            selectAll.checked = false;
        }
        if (
            document.querySelectorAll('.meal_1:checked').length == checkboxes.length
        ) {
            selectAll.checked = true;
        }
    });
});

